      el-form-item(prop="costType", label='')
        el-input(v-model="editCostDatas.costType", placeholder="")
      el-form-item(prop="chargingType", label='')
        el-input(v-model="editCostDatas.chargingType", placeholder="")
      el-form-item(prop="rate", label='')
