        el-input(v-model='editCostDatas.payment', auto-complete='off', placeholder='')
      el-form-item(prop="paymentDate", label='')
        el-input(v-model='editCostDatas.paymentDate', auto-complete='off', placeholder='')
      el-form-item(prop="chargingstart", label='')
        el-input(v-model='editCostDatas.chargingstart', auto-complete='off', placeholder='')
    .dialog-footer(slot="footer")
