</template>

<script>
import {
  MessageBox,
  Message
} from 'element-ui'
import {
  updateCrumbs
} from '@/common/crossers.js'
import AccountCard from '@/components/AccountCard.vue'
import ApproveDialog from '@/components/ApproveDialog.vue'
import moment from 'moment'
import {
  uniqueId,
  remove,
  find,
  // each,
  filter,
  merge
} from 'lodash'

export default {
  components: {
    AccountCard,
    ApproveDialog
  },
  mounted() {
    if (this.$route.params.id !== 'add') {
      const name = this.$route.params.id
      updateCrumbs.$emit('update-crumbs', [{
        id: 'assetName',
        name: name
      }])
      this.demo = {
        name: name,
        sale: '',
        productName: 'B',
        productCode: 'YZ0408~YZ0413',
        issueRate: '6.5%',
        startDate: '2017-01-04',
        endDate: '2017-04-03',
        term: 90,
        planAmount: '124,534,253.00',
        actualAmount: '124,534,253.00',
        riskRating: '',
        sjDate: '2017-01-01',
        xjDate: '2017-01-02',
        raiseTerm: '1',
        balanceTerm: '12:00',
        yearNum: '365',
        // payFrequency: '',
        // assetType: '',
        // publisher: '',
        // trustee: '',
        // shortName: name,
        // issuePlatform: '',
        // rate: 8,

        // riskLevel: 'L',
        // payDate: '2017-04-03',
        // credit: '',
        // gurantee: name,
        // flow: 'N',
        accounts: [{
          id: uniqueId(),
          checked: true,
          type: '',
          belongto: name,
          name: name,
          bankNum: '2310 8372 6532 098',
          bank: ''
        }]
      }
    }
  },

  methods: {
    addEaa() {
      this.$refs.approveVisi.open()
    },
    editCost(cost) {
      this.costTitle = ''
      this.editDialog = true
      this.editCostDatas = merge({}, cost)
    },
    addCost() {
      this.costTitle = ''
      this.editDialog = true
      this.editCostDatas = {
        charge: '',
        costType: '',
        chargingType: '',
        rate: '',
        chargingDays: '',
        costBase: '',
        payment: '',
        paymentDate: '',
        chargingstart: ''
      }
    },
    deleteCost(row) {
      MessageBox.confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.costLists = remove(this.costLists, v => {
          return v.id !== row.id
        })
      })
    },
    // EditDialog() {
    //   this.Eaavisibel = true
    //   this.$nextTick(() => {
    //     each(this.Eaadatas, v => {
    //       if (v.EaaName === this.EaaDefault.EaaName) {
    //         this.$refs.accountsTable.toggleRowSelection(v, true)
    //       }
    //     })
    //   })
    // },
    activeCostSave() {
      this.$refs.costForm.validate((valid) => {
        if (valid) {
          const cost = find(this.costLists, v => {
            return v.id === this.editCostDatas.id
          })
          if (cost) {
            merge(cost, this.editCostDatas)
            this.editDialog = false
          } else {
            this.editCostDatas.id = uniqueId()
            this.costLists.unshift(this.editCostDatas)
            this.editDialog = false
          }
        }
      })
    },
    CloseDialog(eaa) {
      MessageBox.confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.Eaadatas = remove(this.Eaadatas, v => {
          return eaa.EaaName !== v.EaaName
        })
      })
    },
    addMore(data) {
      this.costDialog = true
    },
    submitForm() {
      Message.info('')
    },
    cancel() {
      MessageBox.confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.$router.back()
      })
    },
    handleStartDate(value) {
      if (!value) return
      this.demo.startDate = moment(value).format('YYYY-MM-DD')
    },
    handleEndDate(value) {
      if (!value) return
      this.demo.endDate = moment(value).format('YYYY-MM-DD')
    },
    handlePayDate(value) {
      if (!value) return
      this.demo.payDate = moment(value).format('YYYY-MM-DD')
    }
  },
  computed: {
    filterEaa() {
      return filter(this.Eaadatas, v => {
        return ~v.EaaName.indexOf(this.EaaDefault.EaaName)
      })
    },
    filterCostList() {
      return filter(this.costLists, v => {
        return ~v.charge.indexOf(this.chargeName)
      })
    }
  },

  data() {
    return {
      costTitle: '',
      costDialog: false,
      editDialog: false,
      eaaapp: true,
      chargeName: '',
      EaaDefault: {
        EaaName: 'A1',
        process: ''
      },
      editCostDatas: {},
      Eaavisibel: false,
      risksRating: '',
      filter: {
        name: ''
      },
      risks: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }],
      demo: {
        name: null,
        amount: null,
        ratio: null,
        startDate: null,
        term: null,
        payFrequency: null,
        assetType: null,
        publisher: null,
        trustee: null,
        shortName: null,
        issueAmount: null,
        rate: null,
        endDate: null,
        riskLevel: 'L',
        payDate: null,
        credit: null,
        gurantee: null,
        flow: 'N',
        accounts: []
      },
      pickOpt: {
        format: 'yyyy-MM-dd'
      },
      riskLevels: [{
        name: '',
        value: 'L'
      }, {
        name: '',
        value: 'M'
      }, {
        name: '',
        value: 'H'
      }],
      flows: [{
        name: '',
        value: 'N'
      }, {
        name: '',
        value: 'H'
      }, {
        name: '',
        value: 'L'
      }],
      rules: {
        charge: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        costType: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        chargingType: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        rate: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        chargingDays: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        costBase: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        payment: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        paymentDate: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        chargingstart: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]
      },
      costLists: [{
        charge: '',
        costType: '',
        chargingType: ' ',
        rate: '0.2%',
        chargingDays: '360',
        costBase: '',
        payment: '  ',
        paymentDate: '2016-8-8',
        chargingstart: ' 23,533,09.00',
        id: uniqueId()
      }, {
        charge: ' ',
        costType: '',
        chargingType: '',
        rate: ' ',
        chargingDays: ' ',
        costBase: ' ',
        payment: '  ',
        paymentDate: '2016-8-8',
        chargingstart: ' 123,533,09.00',
        id: uniqueId()
      }],
      // emptyObj: {
      //   charge: '',
      //   costType: '',
      //   chargingType: '',
      //   rate: ' ',
      //   chargingDays: '',
      //   costBase: '',
      //   payment: '',
      //   paymentDate: '',
      //   chargingstart: ''
      // },
      Eaadatas: [{
        EaaName: 'A1',
        details: '',
        process: '    '
      }, {
        EaaName: 'B1',
        details: '',
        process: ''
      }, {
        EaaName: 'C1',
        details: '',
        process: '  '
      }]
    }
  }
}
</script>

<style lang="scss">
.accounts-list {
  .box {
    border: 0;
    padding: 10px 0;
    // .box-tab-header {
