    }
    td {
      padding: 10px 8px;
      &.addMore {
        padding: 10px 0;
        text-align: center;
        a {
          display: inline-block;
        }
        i {
          color: #538cc0;
        }
        span {
          margin-left: 5px;
        }
      }
    }
    .el-input {
      .el-input__inner {
        border: none;
      }
    }
  }
}

.examine {
  padding: 15px;
  table {
    width: 100%;
    th {
      font-weight: normal;
      color: #abb1b8;
      // text-align: left;
    }
    td {
      padding: 10px 0;
      text-align: center;
      i {
        color: #c9cdd1;
      }
      span {
        cursor: pointer;
        margin-right: 10px;
        i {
          display: inline-block;
          margin-right: 5px;
          transform: scale(0.8);
          vertical-align: 0;
          color: #c9cdd1;
        }
      }
    }
  }
}

.add-more {
  position: absolute;
  top: 15px;
  right: 15px;
}

.icon-close {
  margin-left: 8px;
  color: #656776;
  font-weight: normal;
  cursor: pointer;
}
</style>



